.container {
  display: flex;
  align-items: center;
}

.controlList {
  display: flex;
  margin-left: auto;

  @media screen and (min-width: 768px) {
    margin-left: 50px;
  }
}

.controlItem {
  cursor: pointer;
  color: var(--secondary-text-color);

  &:hover {
    font-weight: 500;
  }

  &:not(:last-child) {
    margin-right: 10px;
  }
}

.controlItemActive {
  color: var(--primary-text-color);
}

.result {
  color: var(--secondary-text-color);
}