.formContainer{
  background-color: var(--primary-background-color);
  border: 1px solid var(--input-border-color);
  border-radius: 4px;
  width: 100%;
  padding-left: 12px;
  margin-bottom: 10px;
  box-shadow: var(--heavy-shadow);
  transition: border-color 250ms ease;


@media screen and (min-width: 768px) {
  margin-bottom: 15px;
}

  @media screen and (min-width: 1200px){
    margin-bottom: 20px;
     }
   }

.activeFormContainer{
  border-color:  var(--input-focus-color);
}   

.inputElement {
  width: 80%;
  padding: 10px 5px;
  font-size: 0.9rem;
  vertical-align: middle;
  outline: 0;
  border: 0;

  @media screen and (min-width: 768px){
    padding: 15px 15px;
   }
   @media screen and (min-width: 1200px){
    padding-top: 17px;
    padding-bottom: 17px;
   }
}


.labelForActiveForm {
  display: inline-block;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: white;
  border: 1px solid var(--input-border-color);
  vertical-align: middle;
  margin-right: 7px;
  opacity: 0;
  transition: opacity 250ms ease;
  @media screen and (min-width: 1200px){
    width: 20px;
    height: 20px;
    margin-right: 5px;
  }
}

