.spanCheck {
  width: 17px;
  height: 17px;
  display: inline-block;
  border-radius: 50%;
  background-color: var(--primary-background-color);
  border: 1px solid var(--input-border-color);
  position: absolute;
  top: 3px;
  left: 0;
  transition: background 250ms ease; 
  &:hover {
   border-color:  blue;
  }

  @media screen and (min-width: 768px) {
    width: 20px;
    height: 20px;
  }
}

.spanCheckActive {
  background: linear-gradient(45deg, violet, rgb(8, 184, 242))
}

.label {
  position: relative;
  cursor: pointer;
 margin-right: 7px;

 @media screen and (min-width: 1200px){
  margin-right: 5px;
 }
}

.checkbox{
  opacity: 0;
}